/* ▶ 전체 페이지 스타일 */
.page {
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin: 0;
    font-family: 'Pretendard', sans-serif;
    animation: fadeInUp 0.4s ease forwards;
    background-color: transparent;
    min-height: auto;
    overflow-x: hidden;
}

/* ▶ 제목 */
.heading {
    font-size: 2.2rem;
    font-weight: 600;
    text-align: center;
    color: #004225;
    margin-top: 2rem;
    margin-bottom: 1rem;
    border-bottom: 2px solid #00753C;
    padding-bottom: 0.5rem;
}

/* ▶ 폼 전체 */
.form {
    background-color: #ffffff;
    padding: 2rem 3rem;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    border-radius: 0;
    box-shadow: none;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    overflow-x: hidden;
}

/* ▶ 책 제목/저자/출판사/장르 행 */
.row {
    display: flex;
    gap: 1.5rem;
    flex-wrap: nowrap;
    justify-content: space-between;
}

/* ▶ label + input 옆 정렬 */
.inputGroup {
    display: flex;
    align-items: center;
    gap: 1rem;
    flex: 1;
    min-width: 180px;
}

.inputGroup label {
    width: 100px;
    margin-bottom: 0;
    font-weight: 600;
    color: #004225;
    font-size: 1rem;
}

/* ▶ input + textarea */
input,
textarea {
    padding: 1rem;
    border: 1px solid #ccc;
    border-radius: 10px;
    font-size: 1rem;
    width: 70%;  /* input의 가로 길이를 70%로 줄임 */
    box-sizing: border-box;
    background-color: #fefefe;
    transition: border-color 0.2s;
}

input:focus,
textarea:focus {
    border-color: #00753C;
    outline: none;
}

/* ▶ 감상문 + 이미지 병렬 정렬 */
.reviewImageRow {
    display: flex;
    gap: 1.5rem;
    justify-content: space-between;
    flex-wrap: nowrap;
}

/* ▶ 감상문 가로 길게 만들기 */
.reviewBox {
    flex: 3;  /* 감상문 칸을 더 길게 */
    display: flex;
    flex-direction: column;
}

/* ▶ 감상문 텍스트 스타일 */
textarea {
    resize: none;
    height: 200px;  /* 높이를 일정하게 설정 */
    line-height: 1.6;
    width: 100%;  /* 감상문을 꽉 채우게끔 */
    margin-right: 0; /* 오른쪽 여백을 없애기 */
    font-size: 1rem;  /* 책 제목 스타일에 맞춘 글씨 크기 */
    font-weight: 600;  /* 책 제목 스타일에 맞춘 글씨 두께 */
    color: #004225;  /* 책 제목 스타일에 맞춘 색상 */
}

/* ▶ 사진 첨부 버튼 왼쪽 정렬 */
.imageBox {
    flex: 1;
    display: flex;
    flex-direction: column;
}

/* ▶ 사진 첨부 버튼 */
.imageButton {
    margin-top: 10px;
    padding: 0.75rem 1.25rem;
    background-color: #00753C;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s;
    text-align: center;
    width: 200px;
}

.imageButton:hover {
    background-color: #005b2c;
}

/* ▶ 저장 버튼 */
.submitBtn {
    background-color: #00753C;
    color: white;
    border: none;
    border-radius: 8px;
    padding: 1rem;
    font-size: 1.1rem;
    cursor: pointer;
    margin-top: 1rem;
    transition: background-color 0.3s;
    align-self: flex-end;
    width: 180px;
}

.submitBtn:hover {
    background-color: #005b2c;
}

/* ▶ 오류 메시지 */
.error {
    color: red;
    font-weight: bold;
    text-align: center;
    margin-bottom: 1rem;
}

/* ▶ 등장 애니메이션 */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* ▶ 반응형 모바일 처리 */
@media (max-width: 1024px) {
    .form {
        padding: 1rem;
        width: 100%;
    }

    .row,
    .reviewImageRow {
        flex-direction: column;
        gap: 1rem;
    }

    .inputGroup {
        flex-direction: column;
        align-items: flex-start;
    }

    .inputGroup label {
        width: auto;
        margin-bottom: 0.5rem;
    }

    .reviewBox,
    .imageBox {
        min-width: 100%;
    }

    .submitBtn {
        width: 100%;
        align-self: stretch;
    }
}

/* ▶ label에서 *만 빨간색으로 변경 */
.inputGroup label span {
    color: red;
    font-weight: bold;
}
